.chatbot-container{
    width: 100%;
    height: 100%;
    background-color: #ffffff;

    .record-wrapper{
        position:absolute;
        width: 100%;
        height: 100%;
        top:0;
        background-color: rgba(0,0,0,0.5);

        .record-btn {
            display: block;
            background-color: #0288d1;
            border-radius: 4px;
            margin: 7px 0;
            padding: 11px;
            color: #fff;
        }
        .recording-icon {
            width: 20px;
        }
    }
    
    .record-child-center{
        position: absolute;
        top: 50%;
        width: 100%;
        transform: translate(-50%, -50%);
        left: 50%;
        width: 50%;
        text-align: center;
        background-color: #fff;
        padding: 25px;
        border-radius: 8px;
    }



    .bot-bg {
        height: 100vh;
        width: 100%;
        overflow: hidden;
    }
    .bot-bg img {
        height: auto;
        width: 100%;
    }
    
    .chatbot-header {
       /*  background-color: $header-bg-color; */
        height: $header-footer-size;
        line-height: $header-footer-size;        
        text-align: center;
       /*  border: 1px solid $primary-border-color;   */ 
        position: absolute;
        top: 0;
        width: 100%;     
        z-index: 9999;
        
        .bot-app-icon, .bot-app-txt{
            display: inline-block;
        }
        .bot-app-icon{
            margin: 0 5px 0 20px;
            height: 29px;
            vertical-align: middle;
        }
        .bot-app-txt{
            font-weight: bold;
            color: #fff;
            position: absolute;
            left: 50%;
            transform: translate(-50%);
        }

        .back-icon {
            display: inline-block;
            float: left;
            padding: 0 0 0 19px;
            width: 49px !important;
            margin: 15px 0;
        }

    }
    
    .content-wraper{
        display: table;
        width: 100%;
        background-size: cover;
        content: "";
        clear: both;
        position: absolute;
        top: 0;
        text-align: center;
        
        .bot-list-wrapper{
            height:$bot-list-height;
            overflow: auto;

            .bot-list{           
                .disable{
                    opacity: 0.7;
                }

                li {
                    .bot-list-item {
                        display: block;
                    }

                    display: inline-block;
                    background-color: $bot-list-item-bg;
                    border-radius: 40px;
                    height: 80px;
                    width: 80px;
                    margin: 70px 34px;
                    border: 2px solid $bot-list-item-border;
                    vertical-align: top;

                    .bot-icon-item{
                        height:50px;
                        margin: 14px 0 0;
                    }

                    .bot-list-item-text {
                        display: inline-block;
                        margin: 23px 0;
                        color: white;
                        font-size: 1.2em;
                        line-height: 1.3em;
                    }
                }
            }
        }
        

        .form-label {
            color: $form-label-color;
        }
        .chatbot-name-input{
            text-align: center;
            display: inline-block;
            margin: 20px 0;
            width: 100%;
        }

        .form-control-input {
            display: inline-block;
            width: 85%;
            height: 34px;
            padding: 6px 12px;
            font-size: 14px;
            line-height: 1.42857143;
            color: #555;
            background-color: #fff;
            background-image: none;
            border: 1px solid #ccc;
            border-radius: 9px;
            -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
            -webkit-transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
            -o-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
            transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
        }
    }

    /* chatbot content */
    .chatbot-content {  
        display: table;
        width: 100%;
        /* height:$message-container-size; */
        background-size: cover;
        /* background-image: url("../../images/background/background.jpg"); */  
        content: "";
        clear: both;
        position: absolute;
        bottom: $header-footer-size;

        .chatbot-messages {   
            display: table-cell; 
            vertical-align: bottom;
            /* border: 1px solid #bababa; */
            border-top:none;
            border-bottom:none;
            height:$message-container-size;

            
            
            .messages-wrap{
                vertical-align: bottom;
                overflow: auto;
                max-height: $message-container-size;                

                .chatbot-message{               
                    width:100%;
                    margin: 10px 0;
                    @include clearfix();
    
                    .bot-icon{
                        display: inline-block;
                        padding: 10px;
                        width: 46px;
                        float: left;
                    }

                    .message-text-wraper {
                        float: left;
                        background-color: $msg-bg-color;
                        color: $msg-text-color;
                        /* border: 1px solid $msg-text-color; */
                        @include border-radius(2px 9px 9px 9px);

                        .bot-typing{
                            height:20px;
                            vertical-align: text-top;
                        }
                    }
                    
                    .message-time {
                        width: 72px;
                        float: left;
                        text-align: right;
                        color: $message-time-color;
                        font-size: 0.7em;
                    }

                    .carousel-container{
                        width:99vw;
                        margin: 10px 0 20px;
                        @include clearfix();

                        .swiper-pagination {
                            position: relative;
                        }

                        .swiper-container {
                            /* text-align: center; */

                            .carousel-img{
                                width:69vw;
                                border: 1px solid #eeeeee;
                                border-radius: 9px;
                            }

                            .swiper-slide {
                                /* margin-left: 35px; */
                                /* text-align: center; */
                                width: 80%;
                            }
                            
                            .swiper-slide-bg {
                                /* border: 1px solid #eeeeee;
                                border-radius: 9px;
                                background-color: white; */
                                width: 69vw;

                                background-color: rgba(0,0,0,.5);
                                color: $primary-bg-color;
                                /* border: 1px solid #18ffff; */
                                border-radius: 12px;
                            }

                            .swiper-slider-width{
                                text-align: center;
                                width: 50% ;
                                border-radius: 9px;
                                background-color: $swiper-slider-width-bg;
                                padding:10px 20px;
                                color:$msg-text-color;
                                border:1px solid $primary-bg-color;
                                /* margin:0 20px; */
                            }

                            .swiper-slider-text{
                                text-align: center;
                                border-radius: 9px;
                                background-color: $swiper-slider-width-bg;
                                padding:10px 20px;
                                margin: 10px 30px;
                            }
                            
                            .swiper-slider-action-text{
                                text-align: center;
                                padding:10px 20px;
                                margin: 10px 30px;

                                background-color: $primary-bg-color;
                                color: #fff;
                                /*  border: 1px solid $primary-bg-color; */
                                border-radius: 9px;
                            }

                            .swiper-slider-title-sub{
                                padding:10px 20px;
                            }
                        }
                    }
                }
    
                .user-message{
                    width:100%;
                    margin: 10px 0;
                    @include clearfix();

                    .user-icon{
                        display: inline-block;
                        padding: 10px;
                        width: 46px;
                        float: right;
                    }   

                    .message-text-wraper {
                        float: right;  
                        background-color: $usermsg-bg-color;
                        color:$usermsg-text-color;
                        border: 1px solid $msg-text-color;
                        @include border-radius(9px 2px 9px 9px); 


                        .small {
                            height: 20px;
                            overflow:hidden;
                           
                        }
                        .big {
                            height: auto;
                        }

                        .menuTextToggle{                           
                            background-size: contain;
                            background-repeat: no-repeat;
                            display:none;
                            width: 35px;
                            height: 21px;
                        } 
                        .menuTextActive{
                            display:block !important;

                            img{
                                width:35px;
                                height:35px;
                            }
                        }           
                    }  
                    .message-time {                        
                        width: 72px;
                        float: right;
                        text-align: left;
                        color: $message-time-color;
                        font-size: 0.7em;
                    }               
                }
                
                .message-text-wraper {
                    display: inline-block;                    
                    padding: 10px 20px;
                    width: 78vw;
                    vertical-align: top;
                    margin: 1px 0;  
                    font-size:0.9em;
                    line-height:1.5em;     
                }

                .message-time {                   
                    font-size: 0.6em;
                    margin: 0 0 10px;
                    @include clearfix();
                }
            }
            
        }
    }
    
    /* hamburger-menu */
    .hamburger-menu{
        position: absolute;       
        width:100%;
        z-index: 2;
        /* transition: all 0.3s ease-in-out; */
        /* display:none; */
        
        bottom: 51px;
        
        li a{
            width: 100%;
            height: 40px;
            line-height: 40px;
            text-align: left;
            padding: 0 10px;
            background: $hamburger-menu;
            border: 1px solid $footer-color;
            color:$hamburger-menu-text;
            font-weight:bold;
            display: inline-block;

            .right-link-icon {
                width: 27px;
                float: left;
                padding: 7px 0;
                margin-right: 13px;
            }
        }

        li a:active, li a:hover, li a:focus{
            border: none;
        }
    }
    
    /* Menu Toggle Animation */
    @keyframes moveUp {
        0%  {       
            opacity: 0;
            display: none;
        }
        1%  {           
                bottom: 0 ;
                opacity: 0;
                display: block;
        }
        100%{
                bottom:  51px ;
                opacity: 1;
                display: block;
        }
    }
    @keyframes moveDown {
        0%  {
            opacity: 1;

        }
        99%  {
            bottom: 0 ;
            opacity: 0;
            display: block;
        }
        100%{
            bottom: 51px ;
            opacity: 0;
            display: none;
        }
    }
    .moveUp {
        animation: 0.3s moveUp forwards ;
        transition-timing-function:ease-in-out;

    }
    .moveDown {
        animation: 0.3s moveDown forwards;
        transition-timing-function:ease-in-out;
        display: none;
    }
    /* End of Menu Toggle Animation */

    /* chatbot footer */
    .chatbot-footer {
        background-color: $footer-color;
        border-radius: 0;
        height: $header-footer-size;
        position: absolute;
        bottom: 0;
        width: 100%;       
        /* border: 1px solid #bababa;  */
        z-index: 3;

        .footer-list{
            margin: 0;
            padding: 0;
            list-style-type: none;   
            width: 100%;
            height: 100%;

            li{
                display: inline-block;
                vertical-align: middle;
                line-height: 50px;
                text-align: center;

                .ft-icon{
                    vertical-align: middle;
                    height: $footer-icon-size;
                } 
                .enter-icon, .microphone-icon{
                    height: 23px;
                } 

                form{
                    margin:0;
                    padding:0;
                    height: 0;
                }
                .user-input{
                    padding: 8px;
                    margin: 0;
                    border: 0;
                    width: 100%;
                    resize: none;
                    height: 37px;
                    top: -2px;
                    position: relative;
                    line-height: 20px;
                    background-color:$user-input-bg;
                    color:$typing-input-text-color;
                    border-radius: 9px;
                }

                .user-input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                  color:$typing-input-textplaceholder-color;
                }
                .user-input::-moz-placeholder { /* Firefox 19+ */
                  color: $typing-input-textplaceholder-color;
                }
                .user-input:-ms-input-placeholder { /* IE 10+ */
                  color: $typing-input-textplaceholder-color;
                }
                .user-input:-moz-placeholder { /* Firefox 18- */
                  color: $typing-input-textplaceholder-color;
                }                
                .user-input.user-input:placeholder{
                    color: $typing-input-textplaceholder-color;
                }

                .user-input:focus, .user-input:focus{
                    outline: none;
                }

            }

            .menu-sec, .input-type-sec {
                float: left;
                width: 10vw; 
            }
            .enter-sec, .micro-sec{
                float: right;
                width: 9vw; 
            }
            .input-sec {
                text-align: center;
                margin: 0 auto;
                width: 76vw;            
            }


        }
    }

}


/* hide Scroll bar */
.bot-list-wrapper::-webkit-scrollbar , .messages-wrap::-webkit-scrollbar{
    display: none;
}
html{
    overflow:hidden;
}
.bot-list-wrapper , .messages-wrap{ 
  /*  overflow: -moz-scrollbars-none !important; 
  -ms-overflow-style: none; */
   overflow:auto;
}


