
/* ====== COMMON MIXINS :: Start */

@mixin font2 () {
    font-family: 'Roboto Condensed', 'Roboto', Arial, sans-serif;
}
@mixin opacity ($opacity: 1) {
    zoom: 1;
    filter: alpha(opacity=$opacity*100);
    opacity: $opacity;
}

@mixin border-radius($radius: 4px) {
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    border-radius: $radius;
}

@mixin box-shadow ($shadow-properties...) {
    @if length($shadow-properties) < 1 {
        $shadow-properties: 0 1px 3px rgba(0, 0, 0, 0.3);
    }
    -webkit-box-shadow: $shadow-properties;
    -moz-box-shadow: $shadow-properties;
    box-shadow: $shadow-properties;
}
@mixin diagonal-gradient($color1, $color2) {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#4a287a+0,ed7171+100 */
    background: $color1;/* Old browsers */
    background: -moz-linear-gradient(-45deg, $color1 0%, $color2 100%);/* FF3.6+ */
    background: -webkit-gradient(linear, left top, right bottom, color-stop(0%, $color1), color-stop(100%, $color2));/* Chrome,Safari4+ */
    background: -webkit-linear-gradient(-45deg, $color1 0%, $color2 100%);/* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(-45deg, $color1 0%, $color2 100%);/* Opera 11.10+ */
    background: -ms-linear-gradient(-45deg, $color1 0%, $color2 100%);/* IE10+ */
    background: linear-gradient(135deg, $color1 0%, $color2 100%);/* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#{$color1}', endColorstr='#{$color2}', GradientType=1);/* IE6-9 fallback on horizontal gradient */
}
@mixin transition ($properties...) {
    -webkit-transition: -webkit-#{$properties};
       -moz-transition: -moz-$properties;
        -ms-transition: -ms-$properties;
         -o-transition: -o-$properties;
            transition: $properties;
}

@mixin transform ($properties...) {
    -webkit-transform: -webkit-$properties;
    -moz-transform: -moz-$properties;
    -ms-transform: -ms-$properties;
    -o-transform: -o-$properties;
    transform: $properties;
}

@mixin vgap ($height: 5px) {
    clear: both;
    width: 100%;
    height: $height;
    display: block;
    zoom: 1;
}

@mixin appearance ($value: none) {
    -webkit-appearance: $value;
    -moz-appearance: $value;
    appearance: $value;
}

@mixin transform-origin ($percent) {
    -ms-transform-origin: $percent;
    -webkit-transform-origin: $percent;
    -moz-transform-origin: $percent;
    transform-origin: $percent;
}

@mixin animation ($animation-values) {
    -webkit-animation: $animation-values;/* Safari 4+ */
    -moz-animation: $animation-values;/* Fx 5+ */
    -ms-animation: $animation-values;/* Fx 5+ */
    -o-animation: $animation-values;/* Opera 12+ */
    animation: $animation-values;/* IE 10+, Fx 29+ */
}

@mixin perspective ($value: 300px) {
    -webkit-perspective: $value;/* Chrome 12+, Safari 4+ */
    -moz-perspective: $value;
    -ms-perspective: $value;/* IE 10 */
    -o-perspective: $value;
    perspective: $value;
}

@mixin backface-visibility () {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    backface-visibility: hidden;
}

@mixin background-image($background-image) {
    background-image: url($background-image);
}

@mixin black-overlay-gradient() {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0.2+0,0.83+100 */
    background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0.83) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.83) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.83) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#33000000', endColorstr='#d4000000',GradientType=0 ); /* IE6-9 */
}

@mixin clearfix() {
    display: block;
    content: "";
    clear: both;
}






/* ====== COMMON MIXINS :: End */