// Typography
$font-style: normal;
$font-variant: normal;
$font-weight: normal;
$font-color: #222;
$font-size: 1rem;
$line-height: 2.8;

@font-face {
    font-family: RobotoSlab;
    src: url(../../dist/fonts/RobotoSlab-Light.ttf);
}

$font-family: 'Open Sans', 'RobotoSlab', 'Roboto', Arial, sans-serif;  



// MAIN COLOR
$primary-bg-color: #0288d1;  		// This is primary color for header, footer, message color
$primary-border-color: #bababa; 	// Border Color
$input-bg-color: #ffffff; 			// Typing Input Color
$user-primary-color: #9dd7fe;		// user message color

// OTHER
$bot-goto-bg-color: #18ffff; // go to bot button color
$header-footer-size: 51px;			// Header and Footer Size in View Port Height Proportion //8vh

// BOT LIST
$bot-list-height: calc( 100vh - 0 );
$bot-list-item-bg: #0288d1; // background image for bot icon list on home page
$bot-list-item-border: rgba(0,0,0,0);; // border color for bot icon list on home page

//HEADER
$header-bg-color: rgba(0,150,136,0.8);  // header background color
$header-color: #18ffff; // header color

// MESSAGE
$message-container-size: calc( 100vh - 102px ); 		// Message container Height 84vh
$message-time-color: #fff;
$msg-bg-color: #0288d1; //message bg color 
$msg-text-color: #ffffff;  // message color

//USER MESSAGE
$usermsg-bg-color: #fff; //user message bg color 
$usermsg-text-color: #000; //user message bg color

//SLIDER WIDTH
$swiper-slider-width-bg :#1b3f43;
$swiper-slider-width-text :#fff;

// HAMBERGER MENU
$hamburger-menu: #1b3f43;
$hamburger-menu-text: #fff;
$form-label-color: #fff;

//	FOOTER COLORS
$footer-color: #02292e ;// footer bg color
$user-input-bg : #1b3f43; //user input background 
$footer-icon-size: 17px; // Footer Icon Size

// FOOTER INPUT
$typing-input-color: rgba(0,150,136,0.8);  // message color
$typing-input-text-color: #838383;  // message color
$typing-input-textplaceholder-color: #838383

